/**
 * 轉換vuetify color為hex顏色字串
 *
 * @param {String} color 輸入vuetify顏色字串
 * @returns {String} hex 回傳hex顏色字串
 */
function color2hex(color) {

    //speed
    if (color === 'white') {
        return '#fff'
    }
    else if (color === 'black') {
        return '#000'
    }
    else if (color === 'transparent') {
        return '#rgba(0,0,0,0)'
    }

    //split, 'blue-grey lighten-5' => ['blue-grey', 'lighten-5']
    let cc = split(color, ' ')

    //key0, 'blue-grey' => blueGrey
    let key0 = cc[0].trim()
    key0 = camelCase(key0)

    //key1, 'lighten-5' => 'lighten5', undefined => 'base'
    let key1 = cc[1]
    if (key1 === undefined) {
        key1 = 'base'
    }
    key1 = key1.replace('-', '').trim()

    //hex
    let hex = '#666'
    try {
        hex = colors.default[key0][key1]
    }
    catch (e) {
        console.log('can not convert color: ' + color, colors.default)
    }

    return hex
}


export { color2hex }
